import React from 'react';
import './App.css';
import CardMain from "./card";
import CardOne from './cardOne';
import CardTwo from './cardTwo';
// import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import CustomizedBadges from "./context"
// import { Context } from './context';
// import Badge from '@mui/material/Badge';
// import { styled } from '@mui/material/styles';
// import IconButton from '@mui/material/IconButton';



function App() {

  const [counter, setCounter] = React.useState(0);

  const handleIncrement = () => {
    setCounter(counter + 1);
  };
  const handleDecrement = () => {
    setCounter(counter - 1);
  };

  return (
    <div className="App main1">
      
      <div className="cart">
      {/* <h3> {counter} </h3> */}
      <CustomizedBadges counter={counter}/>
      </div>
      <div className="main">
      <div className="card"><CardMain counter={counter} handleIncrement={handleIncrement} handleDecrement={handleDecrement} /></div>
      <div className="card"><CardOne counter={counter}handleIncrement={handleIncrement} handleDecrement={handleDecrement} /></div>
      <div className="card"><CardTwo counter={counter}handleIncrement={handleIncrement} handleDecrement={handleDecrement} /></div>
      </div>
    </div>
  );
}

export default App;

