import React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import './App.css';
// import  { useContext } from 'react';
// import { Context } from './context';



export default function CardMain(props) {

  const one = (e) => {
var a = document.getElementById("firstcart");
    console.log(e.target.name);
    
    if(e.target.name === "ADD TO CART")
    {
        e.target.name = "REMOVE FROM CART";
        a.innerHTML = "REMOVE FROM CART";
       props.handleIncrement();
    }
    else 
    {
        e.target.name = "ADD TO CART";
        a.innerHTML = "ADD TO CART";
        props.handleDecrement();
    }
    // else{
    //   a.value = "ADD TO CART";
    //     a.innerHTML = "ADD TO CART";
      
    // }
    
};
    
  return (
      
    <Card sx={{ minWidth: 275 }}>
      <CardContent>
      <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
        FREE
      </Typography>
      <Typography  variant="h3" component="div">
        $0
      </Typography>
      <hr/>
      <br/>
      <div class= "card1">
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Single User
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} variant="body2">
      ✓  5GB Storage
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} variant="body2">
      ✓  Unlimited Public Projects
      </Typography>
      <Typography sx={{ mb: 1.5 ,fontSize: 16}} variant="body2">
      ✓  Community Access
      </Typography>

      <Typography sx={{ mb: 1.5, fontSize: 16}} color="text.secondary">
      ✗  Unlimited Private Projects
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} color="text.secondary">
      ✗  Dedicated Phone Support
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} color="text.secondary">
      ✗  Free Subdomain
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} color="text.secondary">
      ✗  Monthly Status Reports
      </Typography>
      </div>
      
      
    </CardContent>
    <CardActions className="Button1">

    <Button id="firstcart" variant="contained"  href="#contained-buttons" name="ADD TO CART" onClick = {one}>
        ADD TO CART
      </Button>
      
      <br/><br/>
    </CardActions>   
    </Card>
    
  );
}