import * as React from 'react';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import './App.css';


export default function CardOne(props) {

    const two = (e) => {
        var a = document.getElementById("secondcart");
        // var b = document.getElementById("demo").value;

        // console.log(b)
        if(e.target.name === "ADD TO CART")
        {
          props.handleIncrement();
            e.target.name = "REMOVE FROM CART";
            a.innerHTML = "REMOVE FROM CART";
            // b.innerHTML = "Free one"
            
        }
        else 
    {
       e.target.name = "ADD TO CART";
        a.innerHTML = "ADD TO CART";
        // b.innerHTML = "cost one"
        props.handleDecrement();
    }
   
        
    };
  return (
    <Card sx={{ minWidth: 275 }}>
      <CardContent>
      <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
        PLUS
      </Typography>
      <Typography variant="h3" component="div">
        $9
      </Typography>
      <hr/>
      <br/>
      <div class= "card1">
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  5 User
      </Typography>
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  50GB Storage
      </Typography>
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Unlimited Public Projects
      </Typography>
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Community Access
      </Typography>
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Unlimited Private Projects
      </Typography>
      <Typography sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Dedicated Phone Support
      </Typography>
      <Typography id="demo" value="free" sx={{ mb: 1.5 , fontSize: 16}} variant="body2">
      ✓  Free Subdomain
      </Typography>
      <Typography sx={{ mb: 1.5, fontSize: 16}} color="text.secondary">
      ✗  Monthly Status Reports
      </Typography>
      </div>
      
    </CardContent>
    <CardActions className="Button1">
    <Button id="secondcart" variant="contained" name="ADD TO CART" href="#contained-buttons" onClick={two}>
        ADD TO CART
      </Button>
      <br/>
      <br/>
    </CardActions>
    </Card>
  );
}